export default {
  data() {
    return {
      // main three navs
      showNext: false,
      showFirst: true,
      showThird: false,
      showFeatured: true,

      // destination
      addis: false,
      awash: false,
      bish: false,
      bahirdar: false,

      // first nav booleans
      desti: false,
      resorts: false,
      exp: false,
      well: false,

      featuredResort: false,
    };
  },
  methods: {
    closeNav() {
      this.$emit("close");
    },
    openAddis() {
      if (window.innerWidth < 768) {
        this.showNext = !this.showNext;
        this.showThird = true;

        this.addis = true;
        this.bish = false;
        this.awash = false;
        this.bahirdar = false;
      } else {
        this.showThird = true;
        this.showFeatured = false;
        this.featuredResort = false;
        this.addis = true;
        this.bish = false;
        this.awash = false;
        this.bahirdar = false;
      }
    },
    openSecond() {
      this.showThird = false;
      this.showNext = true;
    },
    openAwash() {
      if (window.innerWidth < 768) {
        this.showNext = !this.showNext;
        this.showThird = true;

        this.awash = true;
        this.addis = false;
        this.bish = false;
        this.bahirdar = false;
      } else {
        this.showThird = true;
        this.showFeatured = false;
        this.featuredResort = false;

        this.awash = true;
        this.addis = false;
        this.bish = false;
        this.bahirdar = false;
      }
    },
    openBish() {
      if (window.innerWidth < 768) {
        this.showNext = !this.showNext;
        this.showThird = true;

        this.bish = true;
        this.awash = false;
        this.addis = false;
        this.bahirdar = false;
      } else {
        this.showThird = true;
        this.showFeatured = false;
        this.featuredResort = false;

        this.bish = true;
        this.awash = false;
        this.addis = false;
        this.bahirdar = false;
      }
    },
    openBahirdar() {
      if (window.innerWidth < 768) {
        this.showNext = !this.showNext;
        this.showThird = true;
        this.featuredResort = false;

        this.bahirdar = true;
        this.bish = false;
        this.awash = false;
        this.addis = false;
      } else {
        this.showThird = true;
        this.showFeatured = false;
        this.featuredResort = false;

        this.bahirdar = true;
        this.bish = false;
        this.awash = false;
        this.addis = false;
      }
    },
    openDesti() {
      if (window.innerWidth < 768) {
        this.showNext = !this.showNext;
        this.showFirst = !this.showFirst;

        this.desti = true;
        this.resorts = false;
        this.well = false;
        this.exp = false;

        this.showFeatured = false;
        this.featuredResort = true;
        this.showThird = false;
      } else {
        this.showNext = true;

        this.desti = true;
        this.resorts = false;
        this.well = false;
        this.exp = false;

        this.featuredResort = true;
        this.showFeatured = false;
        this.showThird = false;
      }
    },
    openResorts() {
      if (window.innerWidth < 768) {
        this.showNext = !this.showNext;
        this.showFirst = !this.showFirst;

        this.resorts = !this.resorts;
        this.desti = false;
        this.well = false;
        this.exp = false;

        this.showFeatured = false;
        this.featuredResort = true;
        this.showThird = false;
      } else {
        this.showNext = true;

        this.resorts = true;
        this.desti = false;
        this.well = false;
        this.exp = false;

        this.showFeatured = false;
        this.featuredResort = true;
        this.showThird = false;
      }
    },
    openExp() {
      if (window.innerWidth < 768) {
        this.showNext = !this.showNext;
        this.showFirst = !this.showFirst;

        this.exp = !this.exp;
        this.resorts = false;
        this.desti = false;
        this.well = false;

        this.showFeatured = false;
        this.featuredResort = true;
        this.showThird = false;
      } else {
        this.showNext = true;

        this.exp = true;
        this.resorts = false;
        this.desti = false;
        this.well = false;

        this.showFeatured = false;
        this.featuredResort = true;
        this.showThird = false;
      }
    },
    openWell() {
      if (window.innerWidth < 768) {
        this.showNext = !this.showNext;
        this.showFirst = !this.showFirst;

        this.well = !this.well;
        this.exp = false;
        this.resorts = false;
        this.desti = false;

        this.showFeatured = false;
        this.featuredResort = true;
        this.showThird = false;
      } else {
        this.showNext = true;

        this.well = true;
        this.exp = false;
        this.resorts = false;
        this.desti = false;

        this.showFeatured = false;
        this.featuredResort = true;
        this.showThird = false;
      }
    },
    beforeEnter(el) {
      console.log("before enter ");
      el.style.transform = "translateX(-100%)";
    },

    enter(el, done) {
      console.log("starting to enter");
      gsap.to(el, {
        duration: 1,
        x: 0,
        ease: "circ.out",
        onComplete: done,
      });
    },
    beforeLeave(el) {
      console.log("before leave ");
      el.style.transform = "translateX(0)";
    },

    leave(el, done) {
      console.log("starting to leave");
      gsap.to(el, {
        duration: 1,
        x: -100 + "%",
        ease: "expo.out",
        onComplete: done,
      });
    },
    afterLeave() {
      console.log("after leave");
    },
  },
};
